import * as React from "react";
import type { HeadFC, HeadProps, PageProps } from "gatsby";

import { IconContext } from "@react-icons/all-files";
import Arrow from "../assets/common/arrow-up-right.svg";
import "../styles/styles.css";

import { IIndexPageContext } from "../types/index.type";

import Banner from "../components/common/banner";
import StackSection from "../components/stack/stackSection";
import PostSection from "../components/posts/postSection";
import ProjectsSection from "../components/projects/projectsSection";
import { HeadSeo } from "../components/common/metadata";
import StaticReviewsSection from "../components/reviews/staticReviewsSection";
import ContactFormSection from "../components/forms/contactFormSection";
import TeamSection from "../components/ourTeam/teamSection";

const IndexPage = ({
  pageContext: { markupData, lang },
}: PageProps<null, IIndexPageContext>) => {
  return (
    <IconContext.Provider value={{ style: { verticalAlign: "middle" } }}>
      <Banner
        title={markupData.bannerTitle}
        titleBlockClassName="w-full"
        childrenBlockClassName="lg:justify-end -order-1 lg:order-none"
      >
        <Arrow className="aspect-square w-[118px] lg:w-full lg:max-w-[222px] h-max" />
      </Banner>
      <StackSection />
      <ProjectsSection lang={lang} />
      <StaticReviewsSection lang={lang} />
      <PostSection lang={lang} />
      {/* <TeamSection lang={lang} /> */}
      <ContactFormSection lang={lang} />
    </IconContext.Provider>
  );
};

export default IndexPage;

export const Head: HeadFC<null, IIndexPageContext> = ({
  pageContext: { markupData, site, lang },
  location,
}: HeadProps<null, IIndexPageContext>) => {
  return (
    <HeadSeo
      title={markupData.head}
      description={markupData.metaDescription}
      siteUrl={site.siteUrl}
      path={location.pathname}
      lang={lang}
    />
  );
};
