import React from "react";

import { graphql, useStaticQuery } from "gatsby";

import {
  IBlogSectionQueryResult,
  IPostPreviewQueryResult,
} from "../../types/post.type";

import PostPreview from "./postPreview";
import TemplateSection from "../common/templateSection";
import { ILang, Theme } from "../../types/common.type";

const PostSection = ({ lang }: ILang) => {
  const postData = useStaticQuery(query) as IBlogSectionQueryResult;

  const sectionMarkup = postData?.markupData?.nodes?.find(
    (node) => node.fields.language === lang
  )?.frontmatter?.blog;

  let posts: IPostPreviewQueryResult[] | null | undefined;

  if (lang in postData)
    posts = postData?.[lang]?.nodes as IPostPreviewQueryResult[] | null;
  else posts = postData?.en?.nodes;

  return (
    <>
      {posts ? (
        <TemplateSection
          theme={Theme.dark}
          title={sectionMarkup?.blogSectionTitle ?? "new in our blog"}
          linkTitle={sectionMarkup?.blogSectionLinkTitle ?? "all posts"}
          section="blog"
          borderB
          className="min-h-[400px]"
        >
          {posts.map(post => <PostPreview
                lang={lang}
                post={post}
                theme={Theme.dark}
                className="py-5"
                markupData={sectionMarkup}
              />
          )}
        </TemplateSection>
      ) : null}
    </>
  );
};

export default PostSection;

export const query = graphql`
  query {
    en: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: "en" } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: 2
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "DD MMM YYYY", locale: "en")
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                width: 400
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
              )
            }
          }
        }
        wordCount {
          words
        }
      }
    }
    ru: allMarkdownRemark(
      filter: {
        fields: { sourceName: { eq: "posts" }, language: { eq: "ru" } }
      }
      sort: { frontmatter: { date: DESC } }
      limit: 2
    ) {
      nodes {
        id
        frontmatter {
          date(formatString: "DD MMM YYYY", locale: "ru")
          title
          text
          cover {
            childImageSharp {
              gatsbyImageData(
                width: 400
                formats: [AUTO, WEBP, AVIF]
                placeholder: BLURRED
              )
            }
          }
        }
        wordCount {
          words
        }
      }
    }
    markupData: allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          blog {
            bannerTitle
            head
            readPostLink
            readingTimeTitle
            readingTimeUnits
            postFooterTitle
            postFooterText
            blogSectionTitle
            blogSectionLinkTitle
          }
        }
      }
    }
  }
`;
